<template>
  <div class="flex-row pd-1">
    <div class="flex-column w-40">
      <form class="flex-column pure-form pure-form-stacked">
        <label for="front">正面</label>
        <textarea class="w-100" name="front" id="front" cols="30" rows="10" required>
        </textarea>
        <label for="back">反面</label>
        <textarea class="w-100" name="back" id="back" cols="30" rows="10" required>
        </textarea>
        <button 
        class="pure-button"
        @click.prevent="createCard">+ 添加卡片</button>
      </form>
      <div class="flex-row mt-auto">
        <button 
        class="ml-auto pure-button"
        @click="toNext(-1)">返回</button>
        <button
        class="ml-1 pure-button pure-button-primary"
        @click="toSave">保存 & 下一步</button>
      </div>
    </div>
    <div class="overflow-y w-60 flex-1 ml-1 card-table-container">
      <table class="card-table w-100 table-fixed pure-table pure-table-bordered">
        <caption>Cards</caption>
        <thead>
          <tr>
            <th class="card-id">#</th>
            <th class="card-front">Front</th>
            <th class="card-back">Back</th>
            <th class="card-control"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(card, index) in cards" :key="card.front">
            <td class="card-id">{{ index + 1 }}</td>
            <td class="card-front">{{ card.front }}</td>
            <td class="card-back">{{ card.back }}</td>
            <td class="card-control">
              <button class="pure-button button-error" @click="toDel(index)">删除</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cards: [],
    }
  },
  mounted() {
    const cards = localStorage.getItem('workshop-cards-cards');
    if (cards) {
      this.cards = JSON.parse(cards);
    }
  },
  methods: {
    createCard() {
      const front = document.getElementById('front');
      const back = document.getElementById('back');
      if (front.value.trim() && back.value.trim()) {
        this.cards.push({
          front: front.value.trim(),
          back: back.value.trim(),
        })
        front.value = '';
        back.value = '';
      }
    },
    toDel(index) {
      this.cards.splice(index, 1);
    },
    toNext(offset) {
      this.$emit('next', offset);
    },
    toSave() {
      if (this.cards.length === 0) {
        this.$alert('error', '请添加卡片');
        return;
      }
      localStorage.setItem('workshop-cards-cards', JSON.stringify(this.cards));
      this.toNext(1);
    }
  }
}
</script>

<style lang="scss" scoped>
.card-table-container {
  max-height: 80vh;
}
.card-table {
  td {
    white-space: pre-line;
    word-break: break-all;
  }
  .card-id {
    width: 5%;
  }
  .card-front {
    width: 40%;
  }
  .card-back {
    width: 40%
  }
  .card-control {
    text-align: center;
    width: 15%;
  }
}

</style>
