<template>
  <div class="flex-row flex-1">
    <sidebar 
    :tabs="tabs"
    :status="status"
    :active="active"
    @click="toClickTab"
    />
    <component
    class="flex-1"
    :is="tabs[active].component"
    @next='jumpTo'
    >
    </component>
  </div>
</template>

<script>
import Generals from './components/generals.vue';
import Cards from './components/cards.vue';
import Publish from './components/publish.vue';

export default {
  components: {
    Generals,
    Cards,
    Publish,
  },
  data() {
    return {
      active: 0,
      tabs: [
        {
          name: '基础信息',
          component: 'generals',
        },
        {
          name: '卡片',
          component: 'cards',
        },
        {
          name: '发布',
          component: 'publish',
        }
      ],
      status: [
        false,
        false,
      ],
      counter: 0,
    };
  },
  methods: {
    toClickTab(index) {
      if (index === this.tabs.length - 1) {
        if (this.counter !== this.status.length) {
          this.$alert('error', '请先完成之前的步骤');
          return;
        }
      }
      this.active = index;
    },
    jumpTo(index) {
      if (index === 1) {
        if (!this.status[this.active]) {
          this.counter += 1;
        }
        this.status[this.active] = true;
      }
      if (this.active + index === this.tabs.length - 1) {
        if (this.counter !== this.status.length) {
          this.$alert('error', '请先完成之前的步骤');
          return;
        }
      }
      this.active += index;
    },
  },
};
</script>

