<template>
  <div class="flex-column flex-center">
    <div v-if="success" class="flex-item-center">
      <p>发布成功</p>
      <p><a href="/cards">前往查看</a></p>
    </div>
    <div v-else class="flex-item-center">
      <button
        class="pure-button"
        @click="next(-1)">返回
      </button>
      <button class="ml-1 pure-button button-success" @click="publish">发布</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      success: false,
      flag: false,
    };
  },
  mounted() {

  },
  methods: {
    next(offset) {
      this.$emit('next', offset);
    },
    publish() {
      if (this.flag) {
        return;
      }
      this.flag = true;
      const generals = JSON.parse(localStorage.getItem('workshop-cards-generals'));
      const cards = JSON.parse(localStorage.getItem('workshop-cards-cards'));
      const user = JSON.parse(localStorage.getItem('user'));
      const data = {
        title: generals.title,
        intro: generals.intro,
        tags: generals.tags,
        category: generals.category,
        author: user.username,
        // eslint-disable-next-line
        cards,
      };
      localStorage.removeItem('workshop-cards-generals');
      localStorage.removeItem('workshop-cards-cards');
      this.$axios.post(this.$url.cards, data)
        .then(() => {
          this.success = true;
          this.flag = false;
        })
        .catch(() => {
          this.flag = false;
        });
    },
  },
};
</script>