<template>
  <div @click="toClick" class="pd-1">
    <form class="flex-column h-100 w-50 pure-form pure-form-stacked" @submit.prevent="submit">
      <label for="Title">标题</label>
      <input type="text" id="title" name="title" v-model="generals.title" required>
      <label for="tree-input">分类</label>
      <tree-input
      :show="show"
      :data="data"
      :default-value="generals.category"
      @focus="show = true"
      @change="toChange"
      />
      <label for="tags">标签</label>
      <input type="text" id="tags" name="tags" v-model="generals.tags">
      <span class="pure-form-message">用逗号','分隔</span>
      <span class="pure-form-message">例子: 英语,托福</span>
      <div class="flex-row flex-wrap">
        <tag
        class="mr-1 mb-1"
        v-for="tag in tags"
        :text="tag"
        :key="tag"
        />
      </div>
      <label for="intro" >介绍</label>
      <textarea name="intro" id="intro" cols="60" rows="10" v-model="generals.intro" required
      minlength="10"
      >
      </textarea>
      <span class="pure-form-message">不少于10个字符</span>
      <button class="ml-auto mt-auto pure-button pure-button-primary" type="submit">保存 & 下一步</button>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      generals: {
        title: '',
        intro: '',
        tags: '',
        category: '',
      },
      prev: '',

      show: false,
      data: [],
    };
  },
  computed: {
    tags() {
      const { tags } = this.generals;
      const { prev } = this;

      // parse tags.
      if (!prev && tags) {
        // when text was pasted.
        return tags.split(',').filter(obj => obj.trim() !== '');
      } else if (tags && tags[tags.length - 1] === ',') {
        // when comma was entered.
        return tags.split(',').filter(tag => tag.trim() !== '');
      } else if (tags && prev.length > tags.length && prev.substr(tags.length - 1).indexOf(',') !== -1) {
        // when string contains comma was deleted.
        return tags.split(',').filter(tag => tag.trim() !== '');
      } else if (tags && prev && prev.split(',') !== tags.split(',')) {
        // when comma was inerted in the middle of text.
        return tags.split(',').filter(tag => tag.trim() !== '');
      } else if (!tags) {
        // when input is empty.
        return [];
      }
      return [];

      // eslint-disable-next-line
      this.prev = tags;
    },
  },
  mounted() {
    const generals = JSON.parse(localStorage.getItem('workshop-cards-generals'));
    if (generals) {
      this.generals = generals;
    }
    this.$axios.get(this.$url.category)
    .then((resp) => {
      this.data = resp.data;
    })
    .catch((err) => {
      this.$alert('error', err.message);
    })
  },
  methods: {
    toChange(data) {
      this.show = false;
      this.generals.category = data;
    },
    toClick(event) {
      const tree = document.getElementById('tree-input-popout');
      if (tree) {
        const rect = tree.getBoundingClientRect();
        const input = document.getElementById('tree-input');
        const rect2 = input.getBoundingClientRect();
        const x = event.clientX;
        const y = event.clientY;
        if (x < rect.left || x > rect.right || y < rect.top || y > rect.bottom) {
          if (x < rect2.left || x > rect2.right || y < rect2.top || y > rect2.bottom) {
            this.show = false;
          }
        }
      }
    },
    submit() {
      localStorage.setItem('workshop-cards-generals', JSON.stringify(this.generals));
      this.$emit('next', 1);
    },
  },
};
</script>

